//grid
//@mixers
//@mixer can be added as property inside a @media were an @extend cannot
@mixin col-minimum {
  float: left; }
@mixin col-1 {
  width: (100% / 12); }
@mixin col-2 {
  width: 2 / 12 * 100%; }
@mixin col-3 {
  width: (100% / 4); }
@mixin col-4 {
  width: (100% / 3); }
@mixin col-5 {
  width: 5 / 12 * 100%; }
@mixin col-6 {
  width: (100% / 2); }
@mixin col-7 {
  width: 7 / 12 * 100%; }
@mixin col-8 {
  width: 8 / 12 * 100%; }
@mixin col-9 {
  width: 9 / 12 * 100%; }
@mixin col-10 {
  width: 10 / 12 * 100%; }
@mixin col-11 {
  width: 11 / 12 * 100%; }
@mixin col-12 {
  width: 100%; }
//@extend classes
//class used to @extend properties into columns and group in main.css
.col-minimum {
  float: left; }
.col {
  min-height: 1px;
  padding: 0 12px; }
.section {
  margin-right: auto;
  margin-left: auto;
  padding: 0 15px;
  @media (min-width: 960px) {
    width: 80%; }
  @media (max-width: 959px) {
    width: 90%; }
  @media (max-width: 524px) {
    width: 100%; } }
.row {
  @include clearfix; }
.mob-1 {
  @include col-1;
  @extend .col-minimum; }
@media screen {
  .smtab-1 {
    @media (min-width: 525px) {
      @include col-1;
      @include col-minimum; } }
  .tab-1 {
    @media (min-width: 768px) {
      @include col-1;
      @include col-minimum; } }
  .lap-1 {
    @media (min-width: 960px) {
      @include col-1;
      @include col-minimum; } } }
